"use client";
import { FaGithub, FaLinkedin, FaEnvelope, FaArrowUp } from "react-icons/fa";

export default function Footer() {
  const scrollTop = () => window.scrollTo({ top: 0, behavior: "smooth" });
  const goToContact = () => {
    const el = document.getElementById("contacto");
    if (el) el.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <footer className="py-8 mt-16">
      <div className="max-w-6xl mx-auto flex items-center justify-between gap-4">
        <div className="text-sm text-gray-500">¬© {new Date().getFullYear()} Patricio Mardones</div>

        <div className="flex items-center gap-6">
          <a href="https://github.com/" target="_blank" rel="noreferrer" className="text-gray-700 dark:text-gray-200 hover:text-blue-500">
            <FaGithub size={20} />
          </a>
          <a href="https://linkedin.com/" target="_blank" rel="noreferrer" className="text-gray-700 dark:text-gray-200 hover:text-blue-500">
            <FaLinkedin size={20} />
          </a>
          {/* correo que lleva a la seccion de contacto */}
          <button onClick={goToContact} className="text-gray-700 dark:text-gray-200 hover:text-blue-500">
            <FaEnvelope size={20} />
          </button>
        </div>

        <div>
          <button onClick={scrollTop} className="flex items-center gap-2 px-4 py-2 bg-gradient-to-tr from-blue-500 to-purple-600 text-white rounded-full shadow-lg hover:scale-105 transition-transform">
            <FaArrowUp /> Subir
          </button>
        </div>
      </div>
    </footer>
  );
}
