"use client";
import { useState } from "react";
import { motion } from "framer-motion";
import { FaPaperPlane } from "react-icons/fa";

export default function ContactSection() {
  const [form, setForm] = useState({ name: "", email: "", message: "" });

  return (
    <section
      id="contacto"
      className="relative py-32 flex flex-col items-center overflow-hidden"
    >
      {/* üå´Ô∏è Capa de luz ambiental sutil */}
      <motion.div
        animate={{
          opacity: [0.25, 0.45, 0.25],
          scale: [1, 1.03, 1],
        }}
        transition={{
          duration: 6,
          repeat: Infinity,
          ease: "easeInOut",
        }}
        className="absolute inset-0 bg-gradient-to-b 
        from-cyan-400/10 via-blue-500/10 to-purple-600/10 
        blur-3xl pointer-events-none"
      />

      {/* üåà T√≠tulo */}
      <motion.h2
        initial={{ opacity: 0, y: 40 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        viewport={{ once: true }}
        className="mt-10 lg:mt-20 text-5xl font-extrabold mb-16 text-center 
        bg-clip-text text-transparent 
        bg-gradient-to-r from-cyan-300 via-blue-400 to-purple-400 
        drop-shadow-[0_0_20px_rgba(56,189,248,0.25)] 
        leading-[1.15] tracking-tight"
      >
        Cont√°ctame
      </motion.h2>

      {/* üßä Formulario */}
      <motion.form
        onSubmit={(e) => e.preventDefault()}
        initial={{ opacity: 0, y: 50 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        viewport={{ once: true }}
        className="relative max-w-xl w-[90%] mx-auto p-10 rounded-3xl
        bg-gradient-to-br from-white/0 via-white/5 to-white/0
        border border-white/10 backdrop-blur-[6px]
        shadow-[0_0_45px_rgba(139,92,246,0.15)] 
        transition-all duration-500 z-10"
      >
        <div className="grid gap-6">
          <input
            className="w-full p-4 rounded-xl bg-white/5 border border-white/10 
                       text-white placeholder-gray-300 focus:outline-none 
                       focus:ring-2 focus:ring-cyan-400/60 transition-all duration-300"
            placeholder="Tu nombre"
            value={form.name}
            onChange={(e) => setForm({ ...form, name: e.target.value })}
          />
          <input
            className="w-full p-4 rounded-xl bg-white/5 border border-white/10 
                       text-white placeholder-gray-300 focus:outline-none 
                       focus:ring-2 focus:ring-cyan-400/60 transition-all duration-300"
            placeholder="Tu correo"
            type="email"
            value={form.email}
            onChange={(e) => setForm({ ...form, email: e.target.value })}
          />
          <textarea
            className="w-full p-4 rounded-xl bg-white/5 border border-white/10 
                       text-white placeholder-gray-300 focus:outline-none 
                       focus:ring-2 focus:ring-cyan-400/60 transition-all duration-300"
            placeholder="Tu mensaje"
            rows={5}
            value={form.message}
            onChange={(e) => setForm({ ...form, message: e.target.value })}
          />
        </div>

        {/* ‚ú® Bot√≥n y contacto alternativo */}
        <div className="flex flex-col sm:flex-row justify-between items-center mt-8 gap-4">
          <motion.button
            whileHover={{
              scale: 1.05,
              boxShadow: "0 0 25px rgba(139,92,246,0.4)",
            }}
            whileTap={{ scale: 0.95 }}
            className="group relative px-8 py-3 rounded-full 
                       bg-gradient-to-r from-cyan-400/70 via-blue-500/70 to-purple-500/70
                       text-white font-semibold shadow-lg flex items-center gap-2 overflow-hidden"
          >
            <span className="relative z-10">Enviar</span>
            <FaPaperPlane className="relative z-10 group-hover:translate-x-1 transition-transform duration-300" />
            <motion.span
              animate={{ opacity: [0.2, 0.5, 0.2] }}
              transition={{ repeat: Infinity, duration: 2, ease: "easeInOut" }}
              className="absolute inset-0 rounded-full bg-gradient-to-r from-cyan-400/20 to-purple-500/20 blur-2xl"
            ></motion.span>
          </motion.button>

          <div className="text-sm text-gray-200 text-center sm:text-right">
            o escr√≠beme a{" "}
            <a
              href="mailto:patricio@example.com"
              className="text-cyan-400 hover:text-purple-400 transition"
            >
              patricio@example.com
            </a>
          </div>
        </div>
      </motion.form>
    </section>
  );
}
