"use client";
import { motion } from "framer-motion";
import { useEffect } from "react";
import dynamic from "next/dynamic";

// Cargamos el fondo en lazy-load para mejorar performance
const Background3D = dynamic(() => import("./Background3D"), { ssr: false });

export default function HeroSection() {
  useEffect(() => window.scrollTo(0, 0), []);

  return (
    <section
      id="inicio"
      className="relative flex flex-col justify-center items-center min-h-screen overflow-hidden bg-[#0b0b0b] text-white"
    >
      <Background3D />

      <motion.div
        initial={{ opacity: 0, scale: 0.8, y: 30 }}
        animate={{ opacity: 1, scale: 1, y: 0 }}
        transition={{ duration: 1.2, ease: "easeOut" }}
        className="z-10 text-center px-6"
      >
        <motion.h2
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3, duration: 0.8 }}
          className="text-gray-400 uppercase tracking-[0.3em] text-sm mb-2"
        >
          Patricio Mardones
        </motion.h2>

        <motion.h1
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.5, duration: 1 }}
          className="text-[4rem] md:text-[6rem] font-extrabold leading-none"
        >
          <span className="text-white">Software</span>{" "}
          <span className="text-transparent bg-clip-text bg-gradient-to-r from-cyan-400 to-teal-300">
            Engineer
          </span>
        </motion.h1>

        <motion.p
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.9, duration: 0.8 }}
          className="mt-6 text-gray-300 max-w-2xl mx-auto text-lg md:text-xl"
        >
          Desarrollador especializado en crear experiencias digitales fluidas y
          de alto rendimiento con{" "}
          <span className="text-cyan-400">Next.js</span>,{" "}
          <span className="text-teal-400">Laravel</span> y dise√±o interactivo.
        </motion.p>

        <motion.div
          initial={{ width: 0, opacity: 0 }}
          animate={{ width: "6rem", opacity: 1 }}
          transition={{ delay: 1.3, duration: 0.8 }}
          className="mt-8 mx-auto h-[2px] bg-gradient-to-r from-cyan-400 to-teal-400 rounded-full"
        />

        <motion.a
          href="#proyectos"
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1.6 }}
          className="inline-block mt-8 px-6 py-3 border border-cyan-400 text-cyan-300 rounded-full hover:bg-cyan-400 hover:text-black transition"
        >
          Ver proyectos
        </motion.a>
      </motion.div>
    </section>
  );
}
